<template>
  <div>
    <Navbar @logout="logout"></Navbar>
    <div class="container-fluid">
      <div class="row">
        <Sidebar></Sidebar>
        <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-0">
          <router-view></router-view>
        </main>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/common/Navbar';
import Sidebar from '@/components/common/Sidebar';

export default {
  components: {
    Navbar,
    Sidebar
  },
  methods: {
    async logout() {
      const api = `${process.env.VUE_APP_URL}/logout`;
      const { data } = await this.$http.post(api);

      data.success ? this.$router.push('/login') : this.$router.go(0);
      console.log(data);
    }
  }
};
</script>

